// supabaseClient.js
import { createClient } from "@supabase/supabase-js";

console.log('=== DEBUG: Inicializao do Supabase Client ===');

// Usando variveis de ambiente
const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseAnonKey = process.env.REACT_APP_SUPABASE_ANON_KEY;

console.log('=== DEBUG: Valores das variveis ===');
console.log('supabaseUrl est definido:', !!supabaseUrl);
console.log('supabaseAnonKey est definido:', !!supabaseAnonKey);

// Verificar se as credenciais so vlidas (no so apenas placeholders)
const isUrlPlaceholder = supabaseUrl && (supabaseUrl.includes('seu-projeto') || supabaseUrl === 'https://seu-projeto.supabase.co');
const isKeyPlaceholder = supabaseAnonKey && supabaseAnonKey.includes('sua_chave_anonima');

if (!supabaseUrl || !supabaseAnonKey || isUrlPlaceholder || isKeyPlaceholder) {
  console.error('=== ERRO: Credenciais do Supabase ausentes ou invlidas ===');
  console.error('supabaseUrl est ausente:', !supabaseUrl);
  console.error('supabaseAnonKey est ausente:', !supabaseAnonKey);
  console.error('supabaseUrl  placeholder:', isUrlPlaceholder);
  console.error('supabaseAnonKey  placeholder:', isKeyPlaceholder);
  console.error('Ambiente atual:', process.env.NODE_ENV);
  console.error('\n=== INSTRUES PARA RESOLVER ===');
  console.error('1. Verifique se o arquivo .env.local existe na raiz do projeto');
  console.error('2. Preencha as seguintes variveis com suas credenciais reais do Supabase:');
  console.error('   - REACT_APP_SUPABASE_URL (encontrada em Settings > API > Project URL)');
  console.error('   - REACT_APP_SUPABASE_ANON_KEY (encontrada em Settings > API > anon public)');
  console.error('3. Reinicie o servidor de desenvolvimento (npm start) aps alterar o arquivo .env.local');
  console.error('\nTodas as variveis de ambiente REACT_APP_ disponveis:', 
    Object.keys(process.env).filter(key => key.startsWith('REACT_APP_')));
}

console.log('=== DEBUG: Criando cliente Supabase ===');
const supabase = createClient(supabaseUrl, supabaseAnonKey);
console.log('Cliente Supabase criado com sucesso');

// Teste inicial de conexo com mais detalhes (no bloqueante)
// Executar de forma assncrona e no bloquear a inicializao
setTimeout(() => {
  console.log('=== DEBUG: Testando conexo com Supabase ===');
  console.log('Tentando acessar a tabela escolas_completa...');

  // Primeiro, vamos verificar se podemos listar as tabelas
  supabase.from('escolas_completa').select('count').single()
    .then(({ data, error }) => {
      if (error) {
        // No logar como erro crtico, apenas como aviso
        console.warn('Aviso ao testar conexo com a tabela:', error.message || error);
        
        // Se for erro de permisso, dar dica especfica
        if (error.code === '42501') {
          console.warn('AVISO DE PERMISSO: A tabela existe mas voc no tem permisso para acess-la.');
          console.warn('Por favor, verifique as polticas de segurana (RLS) no painel do Supabase.');
        }
      } else {
        console.log('Conexo com a tabela estabelecida. Contagem de registros:', data);
        
        // Se conseguimos contar, vamos tentar buscar alguns dados
        return supabase.from('escolas_completa').select('*').limit(1);
      }
    })
    .then((result) => {
      if (result) {
        const { data, error } = result;
        if (error) {
          console.warn('Aviso ao buscar dados de teste:', error.message || error);
        } else if (data) {
          console.log('Dados de teste encontrados:', data.length > 0 ? 'OK' : 'Tabela vazia');
        }
      }
    })
    .catch(err => {
      // No bloquear por erros de teste
      console.warn('Aviso no teste de conexo (no crtico):', err.message || err);
    });
}, 1000); // Aguardar 1 segundo antes de testar

export { supabase };
